import {React, useState} from 'react'
import {Button, Modal} from 'react-bootstrap'

import useIcons from '../useIcons'

import SystemMessageForm from './SystemMessageForm'


export default function SystemMessageEditorModal({chatHistory, setChatHistory}) {
	const [show, setShow] = useState(false);
	const icons = useIcons()
	const handleClose = () => {
		setShow(false);
	}
	const handleShow = () => setShow(true);
	const l = chatHistory.filter(function(val) {return (val.role === 'system') }).map(function(v) {return v.content}).join("/n").slice(0,40) + "..."
	return (
		<>
		  <Button variant="primary" onClick={handleShow}>
			{icons.pencil} {l}
		  </Button>

		  <Modal
			show={show}
			onHide={handleClose}
		  >
			<Modal.Header closeButton>
			  <Modal.Title style={{marginRight:'2em'}}>System Message</Modal.Title>
			  
			</Modal.Header>
			<Modal.Body>
			  <SystemMessageForm  chatHistory={chatHistory} setChatHistory={setChatHistory}  />
			  

			</Modal.Body>
			
		  </Modal>
		</>
	)
}
