import React, { useState , useEffect} from 'react';
import { Form, Row, Col, Button, FormCheck } from 'react-bootstrap';
import TextareaAutosize from 'react-textarea-autosize';

const SystemMessageForm = ({ chatHistory,setChatHistory}) => {
  
  const [systemMessage, setSystemMessage] = useState('')
  //chatHistory.filter(function(val) {return (val.role === 'system') }).map(function(v) {return v.text}).join("/n"))
 
  useEffect(function() {
	  //console.log("La",JSON.stringify(chatHistory))
	  const l = chatHistory.filter(function(val) {return (val.role === 'system') }).map(function(v) {return v.text}).join("/n")
	  //console.log("L",l)
	  setSystemMessage(l)
  },[])
  
  //function getSystemMessage(message) {
	//return chatHistory.filter(function(val) {return (val.role === 'system') }).map(function(v) {return v.text}).join("/n")
  //}
  
  function saveSystemMessage() {
	let filtered = chatHistory.filter(function(val) {return (val.role === 'user' || val.role === 'assistant') })
	filtered.unshift({role:"system",content:systemMessage})
	setChatHistory(filtered)
  }

  return (
    <Form onSubmit={function(e) {e.preventDefault(); return false}}>
      <div className="border p-3 mb-3">
			<Row className="mb-3">
				<Form.Group as={Col} controlId="openaiKey">
				  <textarea 
				   style={{width:'100%', height:'6em'}}
					onChange={(e) => setSystemMessage(e.target.value)}
					onBlur={(e) => saveSystemMessage()}
					value={systemMessage}
				  ></textarea>
				</Form.Group>
			</Row>
		</div>
     
    </Form>
  );
};

export default SystemMessageForm;
